.successfulCard {
    width: 80ch;
    height: 45ch;
    /* background: #9AFF9A; */
    background: linear-gradient(to right, #7b2ff7, #f107a3);
    border-radius: 5px;
    background-size: cover;
    background-position: center center;
    box-shadow: 0px 10px 30px -5px rgba(0, 0, 0, 0.3);
    transition: box-shadow 0.5s;
    will-change: transform;
    border: 15px solid white;
}

.unsuccessfulCard {
    width: 80ch;
    height: 45ch;
    background: salmon;
    border-radius: 5px;
    background-size: cover;
    background-position: center center;
    box-shadow: 0px 10px 30px -5px rgba(0, 0, 0, 0.3);
    transition: box-shadow 0.5s;
    will-change: transform;
    border: 15px solid white;
}

.cardIdentity:hover {
    box-shadow: 0px 30px 100px -10px rgba(0, 0, 0, 0.4);
}

.successful {
    color: white;
    font-size: 30px;
    font-weight: bolder;
}
.unsuccessful {
    color: red;
    font-size: 30px;
    font-weight: bolder;
}
.userDetails {
    color: white;
    font-size: 20px;
    text-align: center;
}

