
  #navbarCss{
    width: 100%;
    height: 100%;
    background-color: black;
    display: flex;
    flex-direction: row;
    font-family: 'Poppins', sans-serif;
    padding-left: 2rem;
    padding-right: 2rem;
}

.brainCss{
    color: white;
    font-weight: 1000;
    margin-top: 1rem;
    margin-bottom: 1rem;
}

.passwordCss{
    background: linear-gradient(#72f, #c1b);
    -webkit-background-clip: text;
    background-clip: "text";
    -webkit-text-fill-color: transparent;
    font-weight: 1000;
    margin-top: 1rem;
    margin-bottom: 1rem;
}

.gihtubBtn{
  position: relative;
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
  margin-left: auto;
  color: #72f;
  border: 2px #72f solid;
  border-radius: 4px;
  outline: 0;
  background: none;

}

.logoutBtn{
  position: relative;
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
  margin-left: 1rem;
  color: #72f;
  border: 2px #72f solid;
  border-radius: 4px;
  outline: 0;
  background: none;
}